// TEXT-VIEWER.ts
//---------------------------
import { Component, Input, OnInit } from '@angular/core';

/**
 * Interface for TextViewerComponent's "data"
 * @property endpoint (required)
 * @property doc (required)
 * @property facsimile (optional)
 */
export interface TextViewerData {
  /*
   * TEIPub server endpoint
   */
  endpoint: string;
  /*
   * XML and ODD paths
   */
  docs: {
    xml: string;
    odd: string;
    id: string;
    view: string;
    channel?: string;
    translation?: boolean;
    download?: string;
    rootPath?: string;
    url?:string;
  }[];

  /*
   * DOWNLOAD FILES
   */
  download?: {
    label?: string;
    file?: string;
    format?: string;
  };

  /*
   * LABELS for TOOLBAR
   */
  labels?: {
    view?: string;
    select?: {
      label?: string;
      type?: {
        [key: string]: string;
      };
    };
    index?: {
      show?: string;
      hide?: string;
    };
    toc?: string;
    loading?: string;
  };
  /*
   * toggle column
   */
  toggleColumn?: boolean;
  /**
  /*
   * IIIF server endpoint
   */
  facsimile?: any;
  /**
   * additional html classes
   */
  classes?: string;
}

@Component({
  selector: 'n7-text-viewer',
  templateUrl: './text-viewer.html',
})
export class TextViewerComponent implements OnInit {
  @Input() data: TextViewerData;

  @Input() emit: any;

  static _loaded = false;

  ngOnInit() {
    if (!TextViewerComponent._loaded) {
      const s = document.createElement('script');
      s.setAttribute(
        'src',
        '/assets/pb-components/dist/pb-components-bundle.js'
      );
      s.setAttribute('type', 'module');
      s.onload = this.onScriptLoaded;
      document.head.appendChild(s);
    }
  }

  displayIndex() {
    if (!this.emit) return;
    this.emit('togglecolumn');
  }

  onScriptLoaded() {
    TextViewerComponent._loaded = true;
  }

  onClick(payload) {
    if (!this.emit) return;
    this.emit('click', payload);
  }
}
