// PAGINATION.ts
//---------------------------

import { Component, Input } from '@angular/core';
import { Button } from '../../shared-interfaces';

/**
 * Interface for PaginationComponent's options (page size select options)
 *
 * @property text (required)
 * @property classes (optional)
 * @property _meta (optional)
 */
export interface Option {
  /**
   * option text value
   */
  text: string;
  /**
   * additional html classes
   */
  classes?: string;
  /**
   * additional info
   */
  _meta?: any;
  /**
   * render as disabled
   */
  disabled?: boolean;
  /**
   * force selection
   */
  selected?: boolean;
}

/**
 * Interface for PaginationComponent's "data"
 *
 * @property links (required)
 * @property first (optional)
 * @property prev (optional)
 * @property next (optional)
 * @property last (optional)
 * @property classes (optional)
 * @property payload (optional)
 * @property _meta (optional)
 */
export interface PaginationData {
  /**
   * innerHtml for the "first" label
   */
  first?: Button;
  /**
   * innerHtml for the "prev" label
   */
  prev?: Button;
  /**
   * innerHtml for the "next" label
   */
  next?: Button;
  /**
   * innerHtml for the "last" label
   */
  last?: Button;
  /**
   * array of "pages" label (as text)
   */
  links: Button[];
  /**
   * page size select
   */
  select?: {
    label?: string;
    options?: Option[];
    payload?: string;
    disabled?: boolean;
  };
  /**
   * additional html classes
   */
  classes?: string;
  /**
   * action click's payload
   */
  payload?: any;
  /**
   * additional info
   */
  _meta?: any;
}

@Component({
  selector: 'n7-pagination',
  templateUrl: './pagination.html'
})
export class PaginationComponent {
  @Input() data: PaginationData;

  @Input() emit: any;

  onClick(payload: any) {
    if (!this.emit) return;

    this.emit('click', payload);
  }

  onChange(payload: any, value) {
    if (!this.emit) return;
    // needs to also emit selected value
    this.emit('change', { inputPayload: payload, value });
  }
}
