import { Component, Input } from '@angular/core';

/**
 * Interface for svg/img icons
 */
export interface ImgIcon {
  /** Path to the resource */
  path: string;
  /** Alt text for <img/> */
  alt?: string;
}

/**
 * Interface for the data widget component's data
 *
 * @property icon (optional)
 * @property text (required)
 * @property subtitle (required)
 * - text (required)
 * - icon (optional)
 * - grow (required)
 */
export interface DataWidgetData {
  /**
   * item icon
   */
  icon?: string;
  /**
   * image icon
   */
  imgIcon?: ImgIcon;
  /**
   * item text
   */
  text?: string;
  subtitle?: {
    /**
     * item subtitle
     */
    text: string;
    /**
     * item subtitle icon
     */
    icon: string;
    /**
     * image icon
     */
    imgIcon?: ImgIcon;
    /**
     * percent rate
     */
    value: number | string;
    payload: any;
  };
  classes?: string;
  /**
   * mouse event payload
   */
  payload: any;
  _meta?: any;
}

@Component({
  selector: 'n7-data-widget',
  templateUrl: './data-widget.html'
})

export class DataWidgetComponent {
  @Input() data: DataWidgetData;

  @Input() emit: any;

  onMouseEnter(payload) {
    if (!this.emit) return;
    this.emit('mouseenter', payload);
  }

  onMouseLeave(payload) {
    if (!this.emit) return;
    this.emit('mouseleave', payload);
  }
}
