// BREADCRUMBS.ts
//---------------------------

import { Component, Input } from '@angular/core';
import { Image } from '../../shared-interfaces';

/**
 * --- REQUIREMENTS ---
 * + Can have a background image
 * + Can have a background color
 * + Can have an internal label
 *
 * + click event
 * + hover event
 */

/**
 * Interface for Avatar's "Data"
 *
 * @property items (required)
 * @property classes (optional)
 *
 */
export interface AvatarData {
  /**
   * user image
   */
  image?: Image;
  /**
   * background color (missing image)
   */
  color?: string;
  /**
   * Font color for the internal label (missing image)
   */
  labelColor?: string;
  /**
   * Internal label (missing image)
   */
  label?: string;
  /**
   * avatar width & height
   */
  size: string | number;
  /**
   * additional html classes
   */
  classes?: string;
  /**
   * data for events
   */
  payload?: any;
}

@Component({
  selector: 'n7-avatar',
  templateUrl: './avatar.html'
})
export class AvatarComponent {
  @Input() data: AvatarData;

  @Input() emit: any;

  onClick(payload) {
    if (!this.emit) return;
    this.emit('click', payload);
  }

  onMouseEnter(payload) {
    if (!this.emit) return;
    this.emit('mouseenter', payload);
  }

  onMouseLeave(payload) {
    if (!this.emit) return;
    this.emit('mouseleave', payload);
  }
}
