import { Component, Input } from '@angular/core';

/**
 * Interface for ProgressLineComponent's "data"
 *
 * @property label (required)
 * @property value (required)
 */
export interface ProgressLineData {
  /**
   * text visualized on the left side
   */
  label: string;
  /**
   * value that has percentage for bar
   */
  value: number;
}

@Component({
  selector: 'n7-progress-line',
  templateUrl: './progress-line.html'
})
export class ProgressLineComponent {
  @Input() data: ProgressLineData;

  @Input() emit: any;
}
