// SIGNUP.ts
//---------------------------

import { Component, Input } from '@angular/core';

/**
 * Interface for Signup field
 *
 * @property id (M)
 * @property label (M)
 * @property inputType (M)
 * @property payload (M)
 * @property error (O)
 * @property hint (O)
 * @property classes (O)
 */
interface SignupField {
  id: string;
  label: string;
  inputType: 'password' | 'text' | 'email';
  payload: any;
  error?: string;
  hint?: string;
  classes?: string;
}

/**
 * Interface for Signup password field (extends SignupField)
 *
 * @property show (O)
 */
interface SignupPasswordField extends SignupField {
  show?: {
    icon: string;
    label: string;
    payload: any;
  };
}

/**
 * Interface for Signup submit
 *
 * @property label (M)
 * @property payload (M)
 * @property error (O)
 * @property classes (O)
 */
interface SignupSubmit {
  label: string;
  payload: any;
  error?: string;
  classes?: string;
}

/**
 * Interface for Signup footer
 *
 * @property label (M)
 * @property action (M)
 * @property classes (O)
 */
interface SignupFooter {
  label: string;
  action: {
    label: string;
    payload: any;
  };
  classes?: string;
}

/**
 * Interface for Signup data
 * login/signup/forgotPassword groups
 *
 * @property login (O)
 * @property signup (O)
 * @property forgotPassword (O)
 */
export interface SignupData {
  login?: {
    email: SignupField;
    password: SignupPasswordField;
    forgotPassword?: {
      title: string;
      label: string;
      payload: any;
    };
    submit: SignupSubmit;
    footer?: SignupFooter;
  };
  signup?: {
    name: SignupField;
    email: SignupField;
    password: SignupPasswordField;
    rePassword: SignupPasswordField;
    submit: SignupSubmit;
    footer?: SignupFooter;
  };
  forgotPassword?: {
    description: string;
    email: SignupField;
    submit: SignupSubmit;
    footer?: SignupFooter;
  };
}

@Component({
  selector: 'n7-signup',
  templateUrl: './signup.html'
})
export class SignupComponent {
  @Input() data: SignupData;

  @Input() emit: any;

  onClick(payload) {
    if (!this.emit) return;
    this.emit('click', payload);
  }

  onChange(inputPayload, value) {
    if (!this.emit) return;
    this.emit('change', { inputPayload, value });
  }

  onInputEnter(inputPayload, value) {
    if (!this.emit) return;
    this.emit('enter', { inputPayload, value });
  }

  onFocus(payload) {
    if (!this.emit) return;
    this.emit('focus', payload);
  }

  onFocusOut(payload) {
    if (!this.emit) return;
    this.emit('focusout', payload);
  }
}
