// TIMELINE.ts
//---------------------------

import { Component, Input, AfterContentChecked } from '@angular/core';
import { DataItem, TimelineOptions } from 'vis-timeline'; // type interfaces

export interface LibOptions extends TimelineOptions {
  /** Specifies how strong the zooming for each scroll tick.
   * Higher zooming friction will slow zooming speed */
  zoomFriction?: number;
}

export interface TimelineData {
  /** ID for the HTML container element */
  containerID: string;
  /** Options for vis-timeline, full reference: https://visjs.github.io/vis-timeline/docs/timeline/#Configuration_Options */
  libOptions: LibOptions;
  /** Sets the timeline instance to the given parameter */
  _setInstance?: any;
  /** Dadaset in vis-js format, full ref: https://visjs.github.io/vis-timeline/docs/timeline/#Data_Format */
  dataSet: DataItem[];
}

@Component({
  selector: 'n7-timeline',
  templateUrl: './timeline.html'
})
export class TimelineComponent implements AfterContentChecked {
  @Input() data: TimelineData;

  @Input() emit: any;

  /** Knows if the component is loaded */
  private _loaded = false;

  /** Dynamically load required node modules */
  private loadModules = async () => ({
    ...await import('vis-timeline/peer'),
    ...await import('vis-data/peer')
  })

  ngAfterContentChecked() {
    if (!this.data || this._loaded) return;
    this._loaded = true;
    setTimeout(() => {
      this.loadModules().then((modules) => {
        // To enable two-way data binding, import { DataSet } from 'vis-data'
        const { Timeline, DataSet } = modules;
        const items = new DataSet(this.data.dataSet);
        const t = new Timeline(
          document.getElementById(this.data.containerID), // container
          items,
          this.data.libOptions // add groups below
        );
        // Set the timeline instance
        if (this.data._setInstance) this.data._setInstance(t);
      });
    });
  }

  onClick(payload) {
    if (!this.emit) return;
    this.emit('click', payload);
  }
}
