//---------------------------
// FILE-SELECTOR.ts
//---------------------------

import { Component, Input } from '@angular/core';

/**
 * Interface for FileSelectorComponent's "data"
 */
export interface FileSelectorData {
  /**
   * Accepted file types
   * @example '.jpg,.png' 'image/*'
   */
  accept?: string;
  /** Allow selection of multiple files */
  multiple?: boolean;
  /** Additional classes for the wrapping element */
  classes?: string;
}

export interface FileSelectorPayload {
  target: HTMLInputElement,
  files: FileList,
  base64: Array<string | ArrayBuffer>
}

@Component({
  selector: 'n7-file-selector',
  templateUrl: './file-selector.html'
})
export class FileSelectorComponent {
  @Input() data: FileSelectorData;

  @Input() emit: (type: string, payload: FileSelectorPayload) => void;

  onFileSelected(eventTarget) {
    if (!this.emit) return;
    const input: HTMLInputElement = eventTarget;
    const iterableFiles = Array.from(input.files);
    Promise
      .all(iterableFiles.map((file) => this.toBase64(file)))
      .then((base64list) => {
        this.emit('change', { target: eventTarget, files: input.files, base64: base64list });
      });
  }

  /** Obtain base64 string for upload and storage */
  toBase64(file: File): Promise<FileReader['result']> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onerror = (err) => reject(err);
      reader.onload = () => resolve(reader.result);
      reader.readAsDataURL(file);
    });
  }
}
