// ADVANCED-AUTOCOMPLETE.ts
//---------------------------

import { Component, Input } from '@angular/core';
import { Anchor } from '../../shared-interfaces';

/**
 * Interface for AdvancedAutocompleteComponent's "data"
 */
export interface AdvancedAutocompleteData {
  results?: AdvancedAutocompleteResponse[];
  actions?: {
    advanced?: {
      text: string;
      anchor?: Anchor;
      classes?: any;
    };
    showMore?: {
      text: string;
      anchor?: Anchor;
      classes?: any;
    };
  };
  fallback: string;
  classes?: any;
  _meta?: any;
}

interface AdvancedAutocompleteItem {
  /**
   * anchor <a>
   */
  anchor?: Anchor;
  /**
   * Rendered text for the result item
   */
  title?: string;
  /**
   * Additional metadata for the result item
   */
  metadata?: Array<{ key?: string; value?: string }>;
}

interface AdvancedAutocompleteResponse {
  items: AdvancedAutocompleteItem[];
  group?: {
    title: string;
    classes?: string;
    icon?: string;
    anchor?: Anchor;
  };
}

@Component({
  selector: 'n7-advanced-autocomplete',
  templateUrl: './advanced-autocomplete.html'
})
export class AdvancedAutocompleteComponent {
  @Input() data: AdvancedAutocompleteData;

  @Input() emit: any;

  onClick(payload) {
    if (!this.emit) return;

    this.emit('click', payload);
  }
}
