//---------------------------
// INNER-TITLE.ts
//---------------------------

import { Component, Input } from '@angular/core';
import { Anchor, Icon } from '../../shared-interfaces';

export interface InnerTitleOptions {
  text: string;
  value: string;
  disabled?: boolean;
  selected?: boolean;
}

export interface InnerTitleButtons {
  anchor: Anchor;
  text: string;
  classes?: any;
  icon?: string;
}

/**
 * Interface for InnerTitleComponent's "data"
 */
export interface InnerTitleData {
  icon?: Icon;
  image?: string;
  title: {
    main: {
      text: string;
      classes?: any;
    };
    secondary?: {
      text: string;
      classes?: any;
    };
  };
  tools?: string;
  actions?: {
    select?: {
      label?: string;
      options: InnerTitleOptions[];
      payload?: any;
    };
    search?: {
      placeholder: string;
      payload: any;
      button?: {
        text: string;
        payload: any;
      };
      _meta?: any;
    };
    buttons?: InnerTitleButtons[];
  };
  classes?: string;
}

@Component({
  selector: 'n7-inner-title',
  templateUrl: './inner-title.html'
})
export class InnerTitleComponent {
  @Input() data: InnerTitleData;

  @Input() emit: any;

  onClick(payload) {
    if (!this.emit) return;

    this.emit('click', payload);
  }

  onChange(payload, value) {
    if (!this.emit) return;

    this.emit('change', { inputPayload: payload, value });
  }

  onInputChange(payload, value) {
    if (!this.emit) return;

    this.emit('change', { inputPayload: payload, value });
  }

  onInputEnter(payload, value) {
    if (!this.emit) return;

    this.emit('search', { inputPayload: payload, value });
  }
}
