import { Component, Input } from '@angular/core';
import { Anchor, Icon } from '../../shared-interfaces';

/**
 * Interface for a single navbar item
 *
 * @property text (required)
 * @property classes (optional)
 * @property link (optional)
 * @property _meta (optional)
 */
export interface NavItem {
  /**
   * text of a single item of the navbar (text and payload are connected)
   */
  text: string;

  /**
   * image url for contextual icon/thumbnail
   */
  image?: string;

  /**
   * icon name/class from icon font
   */
  icon?: Icon;
  /**
   * A secondary icon
   */
  iconSecondary?: Icon;
  /**
   * additional html classes
   */
  classes?: string;
  anchor?: Anchor;
  /**
   * additional info
   */
  _meta?: any;
}

/**
 * Interface for the navbar
 *
 * @property items (required)
 * @property classes (optional)
 * @property payload (optional)
 * @property _meta (optional)
 */
export interface NavData {
  /**
   * list of the items in the navbar
   */
  items: NavItem[];

  /**
   * additional html classes
   */
  classes?: string;
  /**
   * action click's payload
   */
  payload?: any;
  /**
   * additional info
   */
  _meta?: any;
}

@Component({
  selector: 'n7-nav',
  templateUrl: './nav.html',
})
export class NavComponent {
  @Input() public data: NavData;

  @Input() emit: any;

  onClick(payload: any) {
    if (!this.emit) return;
    this.emit('click', payload);
  }

  onMouseEnter(payload: any) {
    if (!this.emit) return;
    this.emit('mouseenter', payload);
  }
}
