import { Component, Input } from '@angular/core';

/**
 * Interface for ContentPlaceholderComponent's "data"
 *
 * @property classes (optional)
 * @property _meta (optional)
 */
export interface ContentPlaceholderData {
  /**
   * array of html classes
   */
  blocks: BlocksData[];
  /**
   * additional info
   */
  _meta?: any;
}

interface BlocksData {
  classes?: string;
  image?: string;
  alt?: string;
}

@Component({
  selector: 'n7-content-placeholder',
  templateUrl: './content-placeholder.html'
})
export class ContentPlaceholderComponent {
  @Input() data: ContentPlaceholderData;
}
