import { Component, Input } from '@angular/core';
import { Icon } from '../../shared-interfaces';

@Component({
  selector: 'n7-icon',
  templateUrl: './icon.html'
})

export class IconComponent {
  @Input() data: Icon;

  @Input() emit: (type: string, payload?: unknown) => void;

  onClick(value) {
    if (!this.emit) return;
    this.emit('click', value);
  }
}
